.cantacts {
    height: 100vh;
    background-image: url('https://www.editionscollectionsdememoire.fr/wp-content/uploads/2014/10/papier-ancien.jpg');
    display: flex;
    justify-content: center;
    align-items: center;
}
.container {
    width: 100%;
    height: 500px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.myCantact {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background-color: rgb(116, 197, 252);
}
.form {
    display: flex;
    flex-direction: column;
    width: 60%;
    border: 1px solid;
}
.send {
    background-color: black;
    color: white;
}

input {
    height: 40px;
}
