.containt {
    width: 80%;
    height: 400px;
    display: flex;
    border-radius: 5%;
    flex-direction: column;
    justify-content: space-between;
    
}
.works {
    height: 100vh;
    background-color: rgb(255, 255, 255);
    display: flex;
    align-items: center;
    display: flex;
    justify-content: space-around;
    padding-top: 100px;
     padding-bottom: 100px;
    
}

.skills {
    width: 100%;
    height: 10%;
    justify-content: center;
    background-color: rgb(255, 255, 255);
    align-items: center;
    display: flex;
    border-radius: 2px;
    border-radius: 2px;
}

.skill {
    width: 100%;
    height: 70%;
    display: flex;
    justify-content: space-between;
}

.span {
    background-color: #000000;
    color: wheat;
    
}
@media screen and (max-width: 800px) {
    .skill {
    flex-direction: column;
    align-items: center;
    padding-top: 30px;
    }
    .containt {

        height: auto;
    }
    .works {
        height: auto;
        padding: 50px 0;
    }
}